import React from "react";
import { graphql } from "gatsby";

import Layout from "../components/layout";
import SEO from "../components/seo";

export default function TheatreTemplate({
  data, // this prop will be injected by the GraphQL query below.
}) {
  const { markdownRemark } = data; // data.markdownRemark holds your post data
  const { frontmatter, html } = markdownRemark;
  return (
    <Layout currentPage="Interests" currentSubPage={"Theatre Reviews"}>
      <SEO
        title={frontmatter.title + " - Theatre Review"}
        keywords={frontmatter.keywords}
        description={
          frontmatter.description
        }
      />
      <div className="blog-post-container content">
        <div className="blog-post">
          <h1>{frontmatter.title}</h1>
          <div
            className="blog-post-content go-moonshine"
            dangerouslySetInnerHTML={{ __html: html }}
          />
        </div>
      </div>
      <footer className="footer go-center">
        <div>---{frontmatter.date}---</div>
        <div className="lcars-bar-slice-bottom"></div>
      </footer>
    </Layout>
  );
}
export const pageQuery = graphql`
  query($slug: String!) {
    markdownRemark(
      frontmatter: { slug: { eq: $slug }, category: { eq: "theatre" } }
    ) {
      html
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        slug
        title
        short
        subcategory
        description
        keywords
      }
    }
  }
`;
